.flight-list {
   min-width: 310px;
   // width: 605px;
   box-shadow: 0 0 3px 0px;
   // border-radius: 3px;
   background-color: #FFF;
   position: relative;

   table {
      font-family: Livvic-Regular;
      border-collapse: collapse;
      text-align: center;
      font-size: rem(12px);

      td,
      th {
         text-align: left;
         padding: 6px;
      }

      th {
         text-align: center;
         background-color: #3D3C42;
         color: #fff;
      }

      tr {
         cursor: pointer;

         //    &:hover {
         //       background-color: hsl(200deg 18% 46% / 53%) !important
         //    }
      }

      thead {
         position: sticky;
         top: 0
      }

      tr:nth-child(even) {
         background-color: #dddddd;
      }
   }

   .refresh-date {
      font-size: rem(13px);
      font-family: Livvic-Bold;
      margin: 0;
      padding: 4px;
      text-align: left;
      background-color: #FFF;
   }

   .board {
      font-size: rem(35px);
      font-family: Skyfont;
      margin: 0;
      text-align: center;
   }

   .table-details {
      display: flex;
      flex-direction: column;
      height: calc(100vh - 240px);
      overflow: scroll;

   }

   .center {
      position: absolute;
      left: 50%;
      top: 12px;
      transform: translate(-50%, -50%);
      display: flex;
      justify-content: center;
   }
}