.app-header {
   height: 70px;
   // color: #FFF;
   display: flex;
   align-items: center;
   justify-content: space-between;
   width: 100%;
   padding: 10px;
   z-index: -1;
   border-bottom: 1px solid gray;
   // font-family: Air-Americana;
   // background-repeat: no-repeat;
   // background-position: 50% !important;
   // background-size: cover !important;

   h1 {
      font-size: rem(26px);
      // margin-bottom: 50px;
      margin: 0;
      text-align: center;
      // text-shadow: 0px 0px 5px black;
      // font-family: Skyfont;
      font-family: Air-Americana;
      // font-family: Skyfont;
      margin-top: 8px;
   }

   nav {
      display: flex;
      gap: 25px;
      cursor: pointer;
      font-size: rem(18px);

      .active {
         text-decoration: underline;
         color: rgba(16, 16, 94, 0.792);
      }

      a {
         // color: #FFF;
         color: #121212;
         // text-shadow: 0px 0px 5px black;
         text-decoration: none;

         &:hover {
            color: #121212;
            text-shadow: 0px 0px 2px white;
         }

      }
   }

   .hamburger-menu {
      cursor: pointer;

      &.hide {
         display: none
      }

      svg {
         height: 20px;
         width: 20px;
      }
   }

   .show {
      display: flex;
   }


   @media (max-width: 768px) {
      .hamburger-menu {

         &.hide {
            display: block
         }
      }

      .show {
         display: flex;
         top: 71px;
         right: 0;
         position: fixed;
         flex-direction: column;
         align-items: flex-start;
         width: 160px;
         height: 350px;
         background-color: white;
         padding: 10px;
         border-radius: 5px 0px 0px 5px;
         z-index: 1;
         // transition: opacity .35s, visibility .35s, height .35s;
      }
   }
}