.flight-preview {
   // display: flex;
   // flex-direction: column;

   .preview {
      &:hover {
         background-color: hsl(200deg 18% 46% / 53%) !important
      }
   }

   .flight-info-container {
      background-color: white;
      border: 1px solid #121212;
      width: 100%;
      min-width: 600px;

      .info {
         font-size: rem(13px);
         text-align: left;
         padding: 10px;
         line-height: 25px;
         cursor: initial;

         span {
            font-size: rem(13px);
            text-decoration: underline;
         }
      }
   }
}

.column-tiny {
   width: 43px;
   min-width: 43px;
   text-align: center !important;
}

.column-short {
   width: 84px;
   min-width: 84px;
   text-align: center !important;
}

.column-long {
   width: 205x;
   min-width: 205px;
}

.column-date {
   width: 116px;
   min-width: 116px;
}

.column-end {
   width: 100%;
   min-width: 220px;
}